<template>
  <div class="home scroll-list-wrap">
    <cube-scroll
            ref="scroll"
            :data="list"
            :options="options"
            @pulling-down="onPullingDown"
            @pulling-up="onPullingUp">
    <div class="g-swiper">
      <div class="m-search">
        <i></i>
        <form  @submit.prevent action="#">
          <input type="search" placeholder="请输入想要的商品" v-model="keyword" @keypress="submit">
        </form>
      </div>
      <div class="m-list">
        <cube-slide :showDots="showDots" :data="items"/>
      </div>
      <div class="m-navTop">
        <router-link tag="div" :to="{name:'recharge'}" class="s-box">
          <img src="../assets/images/cz.png" alt="">
          <span>会员充值</span>
        </router-link>
        <router-link tag="div" :to="{name:'cash'}" class="s-box s-box-yb">
          <img src="../assets/images/yb.png" alt="">
          <span>元宝换购</span>
        </router-link>
        <router-link tag="div" :to="{name:'funding'}" class="s-box">
          <img src="../assets/images/zc.png" alt="">
          <span>项目众筹</span>
        </router-link>
        <router-link tag="div" :to="{name:'notice'}" class="s-box">
          <img src="../assets/images/gg.png" alt="">
          <span>公告</span>
        </router-link>
      </div>
    </div>
    <div class="g-classify">
      <router-link v-for="(v,i) in icons" v-if="i<=6" :to="{name:'classifyDetails',query:{categoryId:v.id,titlename: v.name}}" tag="div" class="m-box">
        <img :src="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.icon" alt="">
        <span>{{v.name}}</span>
      </router-link>
      <router-link :to="{name:'classify'}" tag="div" class="m-box">
        <img src="../assets/images/more.png" alt="">
        <span>更多</span>
      </router-link>
    </div>
    <div class="g-border"></div>
    <div class="g-goods">
      <router-link v-for="(v,i) in list" :key="i" :to="{name:'detail',query:{id:v.id}}" class="m-box" tag="div">
        <div class="s-img">
          <img v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v.carousels[0]" alt="">
        </div>
        <div class="s-msg beyond">{{v.proName}}</div>
        <div class="s-ps">
          <span>积分：{{v.price}}</span>
          <span>PV:{{v.integral}}</span>
        </div>
      </router-link>
    </div>
    </cube-scroll>
  </div>
</template>


<script>
  export default {
    name: "home",
    data() {
      return {
        showDots: false,
        keyword:'',
        items: [],
        icons:[],
        list: [],/*商品数据*/
        options:{/*滚动配置*/
          scrollbar:{fade:true},
          pullUpLoad:{txt:{ noMore: '没有更多数据了' }},
          pullDownRefresh:{stopTime:0,txt:' '}
        },
        bit:{
          page:1,
          sortId:1,
        }
      }
    },
    created(){
      this.swiper();//轮播
      this.category();//分类
      this.goodList();//列表
    },
    methods: {
      swiper(){
        this.$post(this.$bee+'/api/index/listAdvert',{place:1}).then((data)=>{
          if(data.code==200){
            data.data.adverts.forEach((i)=>{
              this.items.push({url:(i.url?i.url:'javascript:void(0)'),image:'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+i.pic})
            })
          }else{
            this.$createToast({
                txt: data.message,
                type: 'txt'
            }).show();
          }
        })
      },
      category(){
        this.$post(this.$bee+'/api/index/listCategory').then((data)=>{
          if(data.code==200){
            data.data.categorys.forEach((i)=>{
              this.icons.push(i);
            })
          }else{
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      },
      goodList(){
        this.$post(this.$bee+'/api/product/listProduct',this.bit).then((data)=> {
          if (data.code == 200&&data.data.pros.length!=0) {
            data.data.pros.forEach((i) => {
              this.list.push(i)
            })
            this.bit.page++;
          } else {
            this.$refs.scroll.forceUpdate()
          }
        })
      },
      submit(e){
        if(e.keyCode === 13) {
          this.$router.push({name:'classifyDetails',query:{keyword:this.keyword,titlename: this.keyword}})
        }
      },
      onPullingDown() {
        this.bit.page=1;
        this.list=[];
        this.goodList();
      },
      onPullingUp() {
        this.goodList();
      }
    },
  }
</script>
<style scoped lang="stylus">
  .home
    padding-bottom 50px
  .g-swiper
    position relative
    .m-search
      width 200px
      height 30px
      background rgba(0, 0, 0, .3)
      border-radius 15px
      position absolute
      left calc(50% - 87.5px)
      top 10px
      z-index 1000
      i
        position absolute
        width 12px
        height 14px
        top calc(50% - 6px)
        left 10px
        background url(../assets/images/search.png) no-repeat center center
        background-size 100% 100%
      input
        background rgba(0,0,0,0)
        width 100%
        display block
        height 30px
        line-height 30px
        color #fff
        font-size 14px
        text-align left
        text-indent 25px
        font-family SimHei
    .m-list
      width 100%
      & >>> .cube-slide
        width 100%
        img
          width 100%
          height 100%
    .m-navTop
      width 350px
      padding-bottom 5px
      margin 0 auto
      background rgba(0, 0, 0, .5)
      display flex
      position absolute
      bottom 0
      left calc(50% - 175px)
      .s-box
        flex 1
        display block
        padding-top 5px
        position relative
        cursor pointer
        span
          display block
          font-size 10px
          color #fff
          margin-top 5px
        img
          width 20px
          display block
          margin 0 auto
        &:after
          width 3px
          background #fff
          height 40px
          content ''
          position absolute
          right -1.5px
          top 5px
        &:last-child
          &:after
            width 0
            height 0
        &.s-box-yb
          img
            width 32.5px
            margin-top 2px
  .g-classify
    display flex
    flex-wrap wrap
    padding-top 20px
    .m-box
      width 25%
      margin-bottom 10px
      img
        width 40px
        height 40px
      span
        font-size 12px
        display block
        margin-top 5px
        color #393939

  .g-border
    height 8px
    width 100%
    background #eaeaea
  .g-goods
    display flex
    flex-wrap wrap
    padding 7.5px 10.75px 0
    .m-box
      box-sizing border-box
      width 172.5px
      border solid 1px #e1e1e1
      border-radius 5px
      overflow hidden
      margin 0 1.5px
      padding-bottom 10px
      margin-bottom 10px
      .s-img
        width 100%
        img
          width 100%
          height 165px
      .s-msg
        font-size 12px
        padding 5px 5px
        margin-top 2.5px
        text-align left
      .s-ps
        text-align left
        padding 0 5px
        span
          font-size 12px
          color #c38b30
          &:nth-child(1)
            color #c38b30
          &:nth-child(2)
            color #b4a4a4
            margin-left 10px
  ::-webkit-input-placeholder
    color:#fff
  :-moz-placeholder
    color:#fff;
  ::-moz-placeholder
    color #fff;
  :-ms-input-placeholder
    color #fff

</style>
